html {
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  padding: 0;
  margin: 0;
  min-width: 420px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Roboto", sans-serif;
  height: 100vh; }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../font/roboto-v20-cyrillic_latin-400.woff2") format("woff2"), url("../font/roboto-v20-cyrillic_latin-400.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../font/roboto-v20-cyrillic_latin-500.woff2") format("woff2"), url("../font/roboto-v20-cyrillic_latin-500.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../font/roboto-v20-cyrillic_latin-700.woff2") format("woff2"), url("../font/roboto-v20-cyrillic_latin-700.woff") format("woff"); }

:root {
  scroll-behavior: smooth; }

body {
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent; }

@-moz-document url-prefix("") {
  body {
    font-weight: 200;
    -moz-osx-font-smoothing: grayscale; } }

main {
  display: block; }

button,
input,
textarea,
select {
  font: inherit; }

input::-ms-clear {
  display: none; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0; }

.chat {
  display: flex;
  flex-direction: column;
  height: 100%; }

.chat-content {
  display: grid;
  align-content: end;
  align-items: end;
  justify-content: end;
  flex-grow: 1;
  padding: 15px 20px 24px 30px;
  overflow: auto;
  background-color: #5c8ab3; }

.chat-message {
  position: relative;
  padding: 3px 20px 11px;
  background-color: #ffffff;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 20px 4px 4px 20px;
  max-width: 425px; }

.chat-message:hover .chat-message-button,
.chat-message:focus .chat-message-button {
  visibility: visible; }

.chat-message:focus-within .chat-message-button {
  visibility: visible; }

.chat-message-text {
  margin: 0; }

.chat-message-name {
  display: inline-block;
  margin-bottom: 2px;
  font-size: 12px;
  line-height: 16px;
  color: #6994ba; }

.chat-message-button {
  position: absolute;
  width: 18px;
  height: 18px;
  top: -5px;
  right: -5px;
  padding: 0;
  visibility: hidden;
  background-color: #ffffff;
  background-image: url("../../img/svg-sprite/icon-close.svg");
  background-repeat: no-repeat;
  background-size: 8px 8px;
  background-position: center;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  border: none;
  border-radius: 50%;
  font: inherit;
  font-size: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  touch-action: manipulation;
  cursor: pointer; }

.chat-message + .chat-message {
  margin-top: 12px; }

.chat-message:last-child {
  border-radius: 20px 4px 20px 20px; }

.chat-message:first-child {
  border-radius: 20px 20px 4px 20px; }

.chat-form {
  display: flex;
  align-items: center;
  padding: 32px 20px;
  background-color: #265681; }

.chat-form-input {
  flex-grow: 1;
  box-sizing: border-box;
  margin-right: 16px;
  padding: 9px 20px 11px;
  border: 2px solid #ffffff;
  background-color: #ffffff; }

.chat-form-input::-webkit-input-placeholder {
  color: #cccccc; }

.chat-form-input::-moz-placeholder {
  color: #cccccc; }

.chat-form-input::-ms-input-placeholder {
  color: #cccccc; }

.chat-form-input::placeholder {
  color: #cccccc; }

.chat-form-input:hover {
  border-color: #56cf4c;
  outline: none; }

.chat-form-input:focus {
  border-color: #9edcff;
  outline: none; }

.chat-form-input,
.chat-form-button {
  border-radius: 50px;
  font: inherit; }

.chat-form-button {
  flex-shrink: 0;
  padding: 9px 20px;
  color: #ffffff;
  background-color: #39b54a;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.15);
  border: 2px solid transparent;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  touch-action: manipulation;
  cursor: pointer; }

.chat-form-button::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 19px;
  height: 20px;
  margin-right: 6px;
  background-image: url("../../img/svg-sprite/icon-send.svg");
  background-repeat: no-repeat; }

.chat-form-button:hover {
  background-color: #56cf4c;
  box-shadow: 0 8px 4px rgba(0, 0, 0, 0.15); }

.chat-form-button:focus {
  border-color: 2px solid #9edcff; }

.chat-form-button:active {
  background-color: #208b44;
  box-shadow: none; }
